<template>
    <div  class="pain-area">
      <div  v-bind:class="{'tool-bar-hidden':!showToolBar_flag,'tool-bar':showToolBar_flag}">
        <ul class="tool-item">
          <li @click="chartTrigger(1)"><a href="javascript:;">性能监测</a></li>
          <li @click="chartTrigger(2)"><a href="javascript:;">性能指标</a></li>
          <li><a href="javascript:;">工具栏...</a></li>

        </ul>
        <div class="tool-show" @click="showToolBar"><span><</span> </div>
      </div>
      <div class="chart-bar" >
        <div id="chart-container" class="chart-container-class hiddenall" ></div>
        <!--<div id="chart-container-2" class="chart-container-class-2 hiddenall" ></div>-->

      </div>
    </div>
</template>

<script>
    export default {
        name: "chartMenu",
      data(){
          return{
            showToolBar_flag:true,
              base:0,
              oneDay:0,
              date:[],
              data:[],
              now:0,

              chartFlag:2

          }
      },
      methods:{
        showToolBar(){
          this.showToolBar_flag=!this.showToolBar_flag;
          console.log(this.showToolBar_flag)
        },

        chartTrigger(s){
          if(s==1){
            this.chartFlag=1;
            console.log(this.chartFlag);
          }else if(s==2){
            this.chartFlag=2;
            console.log(this.chartFlag);

          }
        },

        init_data(){
          this.base = +new Date(2014, 9, 3);
          this.oneDay = 24 * 3600 * 1000;
          this.date = [];

          this.data = [Math.random() * 150];
          this.now = new Date(this.base);
          console.log(this.base,this.oneDay)
        },

        addData(shift){
          this.now = [this.now.getFullYear(), this.now.getMonth() + 1, this.now.getDate()].join('/');
          this.date.push(this.now);
          this.data.push((Math.random() - 0.4) * 10 + this.data[this.data.length - 1]);

          if (shift) {
            this.date.shift();
            this.data.shift();
          }

          this.now = new Date(+new Date(this.now) + this.oneDay);
        },

        drawChart(){
          // 基于准备好的dom，初始化echarts实例
          let myChart = this.$echarts.init(document.getElementById("chart-container"));
          // 指定图表的配置项和数据

          for (var i = 1; i < 100; i++) {
            this.addData();
          };

          let option = {
            title: {
              text: '测试1'
            },
            toolbox: {
              orient:'vertical',
              right:40,
              top:'middle',
              show:true,
              feature: {
                saveAsImage: {
                  show:true
                },
                dataView: {
                  show:true
                },
                restore: {
                  show:true
                },
                dataZoom: {
                  show:true
                }
              }
            },
            dataZoom:[
              {
                type:'slider',
                start:0,
                end:100,
                xAxisIndex: [0],
                filterMode: 'filter'
              },
              {
                type:'slider',
                start:0,
                end:100,
                yAxisIndex: [0],
                filterMode: 'filter'
              }
            ],
            xAxis: {
              type: 'category',
              boundaryGap: false,
              data: this.date
            },
            yAxis: {
              boundaryGap: [0, '50%'],
              type: 'value'
            },
            series: [
              {
                name:'成交',
                type:'line',
                smooth:true,
                symbol: 'none',
                stack: 'a',
                areaStyle: {
                  normal: {}
                },
                data: this.data
              }
            ]
          };

          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
        },

        redrawChart(){
          let myChart = this.$echarts.init(document.getElementById("chart-container"));

          let myChart2 = this.$echarts.init(document.getElementById("chart-container-2"));

          // 这里设置第二个图表
          let option2 =  {
            tooltip : {
              formatter: "{a} <br/>{b} : {c}%"
            },
            toolbox: {
              orient:'vertical',
              right:40,
              top:'middle',
              show:true,
              feature: {
                saveAsImage: {
                  show:true
                },
                dataView: {
                  show:true
                },
                restore: {
                  show:true
                },
                dataZoom: {
                  show:true
                }
              }
            },
            series: [
              {
                name: '业务指标',
                type: 'gauge',
                detail: {formatter:'{value}%'},
                data: [{value: 50, name: '完成率'}]
              }
            ]
          };

          setInterval( () => {
            this.addData();
            myChart.setOption({
              xAxis: {
                data: this.date
              },
              series: [{
                name:'y1',
                data: this.data
              }
              ]
            });
            //这里设置第二个图表
            option2.series[0].data[0].value = (Math.random() * 100).toFixed(2) - 0;
            myChart2.setOption(option2, true);

          }, 5000);
        }


      },
      mounted() {
        this.init_data();

        this.drawChart();

        this.redrawChart();
      },
      computed:{

      }
    }
</script>

<style scoped>
  .pain-area{
    width: 100%;
    height: 800px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    position: relative;

  }
  .tool-bar{
    flex: 0 0 100px;
    height: 100%;
    justify-content: start;
    background-color: #f0f0f0;
    position: relative;
    border-right: 1px solid  #000;

  }

  .tool-bar-hidden{
    flex: 0 0 100px;
    height: 100%;
    margin-left: -100px;
    position: relative;
  }

  .tool-item{
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    margin: 0;
    padding: 0;
  }

  .tool-item li{
    text-decoration: none;
    display: flex;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    align-items: center;
    justify-content: center;

  }

  .tool-item li a{
    text-decoration: none;
    font-size: 20px;
    line-height: 20px;
  }

  .tool-item li:hover{
    background-color: #1e6298;
    border-radius: 10px;

  }

  .tool-item li a:hover{
    color: #fff;

  }

  .tool-show{
    width: 20px;
    height: 50px;
    background-color: #1e6298;
    position: absolute;
    top: 50%;
    left: 100%;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

  }
  .tool-show span{
    color: #fff;

  }


  .chart-bar {
    flex: 1;
    height: 100%;
    padding: 20px 40px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  #chart-container,
  #chart-container-2{
    width: 100%;
    height: 100%;
    z-index: 1000;
  }


  .hiddenall{
    width: 0px;
    height: 0px;
    overflow: hidden;
  }


</style>



