import Vue from 'vue'
import Router from 'vue-router'

import page from '../pages/index.vue'
import monitor from '@/pages/monitor'






Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'page',
      component: page
    },
    {
      path: '/monitor',
      name: 'monitor',
      component: monitor
    }

  ]
})



