<template>
  <section class="hero text-light text-center">
    <div class="container-sm">
      <div class="hero-inner">
        <p class="hero-paragraph is-revealing">Are you sleeping? Are you sleeping?
          Brother John, Brother John?
          Morning bells are ringing. Morning bells are ringing.
          Ding, dang, dong.Ding, dang, dong.</p>
        <p class="hero-cta is-revealing">
          <router-link class="button button-secondary button-shadow" href="#" to="/monitor">
            在线性能监测展示
          </router-link>
        </p>
        <h1 class="hero-title h2-mobile mt-0 is-revealing">主要产品</h1>
        <div class="hero-media">
          <div class="swiper-container">
            <div class="swiper-wrapper">
              <div class="swiper-slide">
                <a href="#"><img src="../assets/img001.jpg" alt=""></a>
              </div>
              <div class="swiper-slide">
                <a href="#"><img src="../assets/img002.jpg" alt=""></a>

              </div>
              <div class="swiper-slide">
                <a href="#"><img src="../assets/img003.jpg" alt=""></a>

              </div>
              <div class="swiper-slide">
                <a href="#"><img src="../assets/img004.jpg" alt=""></a>

              </div>
            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"></div>

            <!-- 如果需要导航按钮 -->
            <div class="swiper-button-prev"></div>
            <div class="swiper-button-next"></div>

          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import 'swiper/dist/css/swiper.css'
  import Swiper from 'swiper';

    export default {

      mounted(){
        new Swiper ('.swiper-container', {
          direction: 'horizontal', // 垂直切换选项
          loop: true, // 循环模式选项
          autoplay:true,
          // 如果需要分页器
          pagination: {
            el: '.swiper-pagination',
          },

          // 如果需要前进后退按钮
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          },

        })
      }
    }
</script>

<style scoped>



  .hero {
    position: relative;
    overflow: hidden;

  }


  .text-center {
    text-align: center;
  }
  .text-light {
    color: rgba(255, 255, 255, 0.8);
  }

  .container-sm {
    max-width: 848px;
    margin: 0 auto;
    padding: 0 24px;
  }

  .button-secondary {
    background: #00CEC5;
    background: linear-gradient(65deg, #00A2B8 -25%, #00F9D2 100%);
  }

  .button {
    font-size: 16px;
    line-height: 16px;
    display: inline-flex;
    padding: 16px 32px;
    cursor: pointer;
    text-align: center;
    text-transform: uppercase;
    border: none;
    border-radius: 2px;
    background-color: #FFFFFF;
    justify-content: center;
    text-decoration: none;
    color: #fff;
  }

  @media (min-width: 641px)
  .hero-media {
    height: 600px;
    margin-top: 64px;
  }
  .hero-media {
    position: relative;
    height: 600px;
    margin-top: 40px;
    border-radius: 4px 4px 0 0;
    background: #FFFFFF;
    box-shadow: 0 16px 48px #b3ebe3;
    overflow: hidden;
  }

  .swiper-container{
    width: 100%;
    height: 100%;
    display: flex;
    /*justify-content: center;*/
    /*align-items: center;*/
  }


</style>



